@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
body {
    background-color: #54595F;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 300;
}
.navbar {
}
.nav-link {
    color: #00eb01;
}

.nav-link:hover{
    color: #00eb01;
}

.mr-nav-link{
    color: #ffffff;
    margin-top: 2px;
    text-decoration: none;
    color: white;
    padding: 10px 35px;
    background-color: #6113b1;
    border-radius: 50px;
    line-height: 21px;
}

.mr-nav-link:hover{
    color: black;
    background-color: #00eb01;
    transition: color .5s ease-in-out, background-color .5s;
}

.profile-btn {
    color: white;
    text-decoration: none;
    margin-top: 10px;
    padding-left: 20px;
    padding-right: 20px;
}

.profile-btn:hover{
    color: #00eb01;
    transition: color .5s;
}
.bg-image {
    padding-bottom: 500px;
    background-repeat: no-repeat;
    text-align: center;
}

.text {
    color: white
}
.block {
    display: block;
}
.convocations-card {
    margin-bottom: 50px;
}
